// Toasts
.toast {
    @include toast-variant($dark-color);
    border: $border-width solid $dark-color;
    border-radius: $border-radius;
    color: $light-color;
    display: block;
    padding: $layout-spacing;
    width: 100%;
    margin-bottom: 1rem;

    &.toast-primary {
        @include toast-variant($primary-color);
    }

    &.toast-success {
        @include toast-variant($success-color);
    }

    &.toast-warning {
        @include toast-variant($warning-color);
    }

    &.toast-error {
        @include toast-variant($error-color);
    }

    &.toast-info {
        @include toast-variant($info-color);
    }

    a {
        color: $light-color;
        text-decoration: underline;

        &:focus,
        &:hover,
        &:active,
        &.active {
            opacity: .75;
        }
    }

    .toast-dismiss {
        cursor: pointer;
        padding-right: 1rem;

        &:hover, &.active {
            opacity: .75;
        }
    }

    .btn-clear {
        margin: 4px -2px 4px 4px;
    }
}
